import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import {useStaticQuery, graphql} from "gatsby"
import Img from "gatsby-image"
import foto1 from "../images/convert-audio-mac.png"
import PostIndex2 from "../components/postIndex2/postIndex2";


const vitmainaD = () => {
    return (
        <Layout sideNavigation={true}>
            <SEO
                title="Vitamina D"
                meta="Toda la información que necesitas sobre la vitamina D. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina D?"
                description="Toda la información que necesitas sobre la vitamina D. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina D?"
                lang="es"
            />

            <h1>¿Qué es la vitamina D?</h1>
            <PostIndex2 linksData={[
                {"url": "#funciones", "text": "Funciones"},
                {"url": "#alimentos", "text": "Alimentos"},
                {"url": "#deficiencia", "text": "Deficiencia"},
                {"url": "#exceso", "text": "Exceso"},
            ]}
            />
            <hr/>
            <p class="introductionText">La vitamina D es una vitamina liposoluble que est&aacute; presente de forma
                natural en muy pocos
                alimentos, a&ntilde;adida a otros y disponible como suplemento diet&eacute;tico. Tambi&eacute;n se
                produce de forma end&oacute;gena cuando los rayos ultravioletas de la luz solar inciden en la piel y
                desencadenan la s&iacute;ntesis de la vitamina D.</p>
            <hr/>
            <p></p>
            <h2 name={"funciones"}>¿Para qué sirve la vitamina D?</h2>
            <p><b>La vitamina D es esencial por varias razones, entre ellas, para mantener los huesos y los dientes
                sanos.
                Tambi&eacute;n puede proteger contra una serie de enfermedades y afecciones, como la diabetes de tipo
                1.</b></p>
            <p>La vitamina D promueve la absorci&oacute;n del calcio en el intestino y mantiene concentraciones
                s&eacute;ricas de calcio y fosfato adecuadas para permitir la mineralizaci&oacute;n normal del hueso .
                Tambi&eacute;n es necesaria para el crecimiento y la remodelaci&oacute;n &oacute;sea . <b>Sin suficiente
                    vitamina D, los huesos pueden volverse delgados, quebradizos o deformados</b>. La suficiencia de
                vitamina D
                previene el raquitismo en los ni&ntilde;os y la osteomalacia en los adultos . Junto con el calcio, la
                vitamina D tambi&eacute;n ayuda a proteger a los adultos mayores de la osteoporosis.</p>
            <p>La vitamina D tiene otros papeles en el cuerpo, incluyendo la modulaci&oacute;n del crecimiento celular,
                la funci&oacute;n neuromuscular e inmune, y la reducci&oacute;n de la inflamaci&oacute;n. Muchos genes
                que codifican las prote&iacute;nas que regulan la proliferaci&oacute;n, diferenciaci&oacute;n y
                apoptosis de las c&eacute;lulas son modulados en parte por la vitamina D.</p>
            <p></p>
            <h2 name={"alimentos"}>Alimentos con vitamina D</h2>
            <p><b>Muy pocos alimentos en la naturaleza contienen vitamina D.</b> La carne de los pescados grasos (como
                el
                salm&oacute;n, el at&uacute;n y la caballa) y los aceites de h&iacute;gado de pescado se encuentran
                entre las mejores fuentes. </p>
            <p>Peque&ntilde;as cantidades de vitamina D se encuentran en el h&iacute;gado de
                la carne, el queso y las yemas de huevo. La vitamina D de estos alimentos se encuentra principalmente en
                forma de vitamina D3. Algunos hongos proveen vitamina D2 en cantidades variables . Tambi&eacute;n se
                dispone de hongos con mayores niveles de vitamina D2 por estar expuestos a la luz ultravioleta en
                condiciones controladas.</p>
            <p></p>
            <ul>

            </ul>
            <li>Aceite de h&iacute;gado de bacalao</li>
            <li>Trucha&nbsp;</li>
            <li>Salm&oacute;n&nbsp;</li>
            <li>Sardinas&nbsp;</li>
            <li>Huevo (yema del huevo)</li>
            <p></p>
            <p></p>
            <h3>Vitamina D del sol</h3>
            <p></p>
            <p>Los factores que afectan a la exposici&oacute;n a la radiaci&oacute;n UV y las investigaciones realizadas
                hasta la fecha sobre la cantidad de exposici&oacute;n al sol necesaria para mantener los niveles
                adecuados de vitamina D dificultan la elaboraci&oacute;n de directrices generales.</p>
            <p><b>Algunos investigadores de la vitamina D han sugerido, por ejemplo, que aproximadamente 5-30 minutos de
                exposici&oacute;n al sol entre las 10 AM y las 3 PM al menos dos veces por semana a la cara, los brazos,
                las piernas o la espalda sin protecci&oacute;n solar suelen conducir a una s&iacute;ntesis suficiente de
                vitamina D y que el uso moderado de las c&aacute;maras de bronceado comerciales que emiten entre el 2% y
                el 6% de radiaci&oacute;n UVB tambi&eacute;n es eficaz</b>. Los individuos con exposici&oacute;n solar
                limitada necesitan incluir buenas fuentes de vitamina D en su dieta o tomar un suplemento para alcanzar
                los niveles de consumo recomendados.</p>
            <p></p>
            <h2 name={"deficiencia"}>Deficiencia de vitamina D</h2>
            <p>Aunque el cuerpo puede crear vitamina D, su deficiencia puede ocurrir por muchas razones.</p>
            <p></p>
            <h3>Efectos</h3>
            <p><b>Tipo de piel:</b> La piel m&aacute;s oscura, por ejemplo, y el protector solar, reducen la capacidad
                del
                cuerpo para absorber los rayos ultravioleta B (UVB) del sol. La absorci&oacute;n de la luz solar es
                esencial para que la piel produzca vitamina D.</p>
            <p></p>
            <p><b>Protector solar:</b> Un protector solar con un factor de protecci&oacute;n solar (FPS) de 30 puede
                reducir la
                capacidad del cuerpo para sintetizar la vitamina en un 95% o m&aacute;s. Cubrir la piel con ropa
                tambi&eacute;n puede inhibir la producci&oacute;n de vitamina D.</p>
            <p></p>
            <p><b>Ubicaci&oacute;n geogr&aacute;fica:</b> Las personas que viven en latitudes septentrionales o en zonas
                de
                alta contaminaci&oacute;n, trabajan en turnos nocturnos o est&aacute;n confinadas en sus casas deben
                tratar de consumir la vitamina D de fuentes alimenticias siempre que sea posible.</p>
            <p></p>
            <p><b>Lactancia materna:</b> Los beb&eacute;s que se alimentan exclusivamente de leche materna necesitan un
                suplemento de vitamina D, especialmente si tienen la piel oscura o una exposici&oacute;n m&iacute;nima
                al sol. La Academia Americana de Pediatr&iacute;a recomienda que todos los lactantes amamantados reciban
                400 unidades internacionales (UI) diarias de vitamina D por v&iacute;a oral.</p>
            <p>Aunque las personas pueden tomar suplementos de vitamina D, es mejor obtener cualquier vitamina o mineral
                a trav&eacute;s de fuentes naturales siempre que sea posible.</p>
            <p></p>
            <h2 name={"exceso"}>Exceso de vitamina D</h2>

            <p>El consumo excesivo de vitamina D puede conducir a la sobrecalcificaci&oacute;n de los huesos y al
                endurecimiento de los vasos sangu&iacute;neos, los ri&ntilde;ones, los pulmones y los tejidos del
                coraz&oacute;n.</p>
            <p></p>
            <p>Los s&iacute;ntomas m&aacute;s comunes del exceso de vitamina D incluyen dolor de cabeza y
                n&aacute;useas. Sin embargo, el exceso de vitamina D tambi&eacute;n puede provocar lo siguiente:</p>
            <p></p>
            <ul>
                <li>P&eacute;rdida de apetito</li>
                <li>Boca seca.</li>
                <li>Un sabor met&aacute;lico</li>
                <li>V&oacute;mitos</li>
                <li>Estre&ntilde;imiento</li>
                <li>Diarrea</li>

            </ul>
            <p>El exceso de vitamina D suele producirse por tomar demasiados suplementos. Es mejor obtener la
                vitamina D
                de fuentes naturales.
            </p>
            <p></p>
            <p></p>
            <p></p>
        </Layout>
    )
}

export default vitmainaD
